<template>
    <el-container>
        <el-main>
            <toolbar
                :buttonList="buttonList"
                @callFunction="callFunction"
            ></toolbar>
            <!--列表-->
            <el-table
                :data="trucks"
                v-loading="listLoading"
                :row-class-name="$tableRowClassName"
                style="width: 100%"
            >
                <!-- <el-table-column type="selection" width="50"> </el-table-column> -->
                <el-table-column type="index" width="80">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>
                <el-table-column prop="siteshortname" label="监测对象">
                </el-table-column>
                <el-table-column
                    prop="truckno"
                    label="车牌号"
                ></el-table-column>
                <el-table-column
                    prop="truckstartdate"
                    :formatter="dataFormat"
                    label="有效期开始日"
                >
                </el-table-column>
                <el-table-column
                    prop="truckenddate"
                    :formatter="dataFormat"
                    label="有效期结束日"
                >
                </el-table-column
                ><el-table-column label="状态">
                    <template slot-scope="scope">
                        <el-tag
                            v-if="
                                getStatus(
                                    scope.row.truckstartdate,
                                    scope.row.truckenddate
                                )
                            "
                            type="success"
                            disable-transitions
                            >有效</el-tag
                        ><el-tag v-else type="info" disable-transitions
                            >无效</el-tag
                        >
                    </template>
                </el-table-column>
                <el-table-column
                    label="操作"
                    width="150"
                    v-if="canEdit || canDelete"
                >
                    <template slot-scope="scope">
                        <el-button
                            v-if="canEdit"
                            plain
                            type="warning"
                            size="small"
                            @click="handleEdit(scope.$index, scope.row)"
                            >编辑</el-button
                        >
                        <el-button
                            v-if="canDelete"
                            plain
                            type="danger"
                            size="small"
                            @click="handleDel(scope.$index, scope.row)"
                            >删除</el-button
                        >
                    </template>
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar">
                <el-pagination
                    layout="total, prev, pager, next, jumper"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="page"
                    :total="total"
                    style="float: right"
                >
                </el-pagination>
            </el-col>

            <!--编辑界面-->
            <el-dialog
                title="编辑"
                :visible.sync="editFormVisible"
                v-model="editFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="editForm"
                    label-width="120px"
                    :rules="editFormRules"
                    ref="editForm"
                >
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="监测对象分组" prop="GROUPID">
                                <el-select
                                    v-model="editForm.GROUPID"
                                    filterable
                                    placeholder="请选择"
                                    @change="groupChange"
                                >
                                    <el-option
                                        v-for="item in groups"
                                        :key="item.GROUPID"
                                        :label="item.groupshortname"
                                        :value="item.GROUPID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="监测对象" prop="SITEID">
                                <el-select
                                    v-model="editForm.SITEID"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="item in selectSites"
                                        :key="item.SITEID"
                                        :label="item.siteshortname"
                                        :value="item.SITEID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>

                        <el-col :span="12">
                            <el-form-item label="车牌号" prop="truckno">
                                <el-input
                                    v-model="editForm.truckno"
                                    auto-complete="off"
                                    maxlength="10"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="运输公司名称" prop="transcomp">
                                <el-input
                                    v-model="editForm.transcomp"
                                    auto-complete="off"
                                    maxlength="50"
                                ></el-input>
                            </el-form-item>
                        </el-col>

                        <el-col :span="12">
                            <el-form-item
                                label="有效期开始日"
                                prop="truckstartdate"
                            >
                                <el-date-picker
                                    v-model="editForm.truckstartdate"
                                    type="date"
                                    placeholder="选择开始日期"
                                >
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item
                                label="有效期结束日"
                                prop="truckenddate"
                            >
                                <el-date-picker
                                    v-model="editForm.truckenddate"
                                    type="date"
                                    placeholder="选择结束日期"
                                >
                                </el-date-picker>
                            </el-form-item>
                        </el-col>

                        <el-col :span="12">
                            <el-form-item label="处置编号" prop="disposeno">
                                <el-input
                                    v-model="editForm.disposeno"
                                    auto-complete="off"
                                    maxlength="15"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row class="form-button">
                        <el-col :span="24">
                            <el-button @click.native="editFormVisible = false"
                                >取消</el-button
                            >
                            <el-button
                                type="primary"
                                @click.native="editSubmit"
                                :loading="editLoading"
                                >提交</el-button
                            >
                        </el-col>
                    </el-row>
                </el-form>
            </el-dialog>
        </el-main>
    </el-container>
</template>
<script>
import {
    // getGroupTruckCount,
    getTruckPage,
    addTruck,
    editTruck,
    deleteTruck,
} from "../../api/api";
import leftgroup from "../../components/LeftGroup";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import dicmixin from "../../assets/dic/dicmixin";
import util from "../../../util/date";
export default {
    components: { leftgroup, Toolbar },
    mixins: [dicmixin],
    data() {
        return {
            groupCounts: [],
            buttonList: [],
            filters: {
                name: "",
            },
            trucks: [],
            total: 0,
            page: 1,
            pageSize: 10,
            listLoading: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择监测对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择监测对象",
                        trigger: "blur",
                    },
                ],
                truckno: [
                    {
                        required: true,
                        message: "请填写车牌号",
                        trigger: "blur",
                    },
                ],
                truckstartdate: [
                    {
                        required: true,
                        message: "请填写有效期开始日",
                        trigger: "blur",
                    },
                ],
                truckenddate: [
                    {
                        required: true,
                        message: "请填写有效期结束日",
                        trigger: "blur",
                    },
                ],
            },
            editForm: {},
            canEdit: false,
            canDelete: false,
            groups: [],
            selectSites: [],
            selectGroupId: 0,
            isEdit: false,
        };
    },
    methods: {
        callbackGroup(item) {
            this.selectGroupId = item.GROUPID;
            this.page = 1;
            this.getTrucks();
        },
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        getTrucks() {
            let paras = {
                keyword: this.filters.name,
                page: this.page,
                size: this.pageSize,
            };
            this.listLoading = true;
            getTruckPage(paras).then((res) => {
                if (res.success) {
                    this.trucks = res.data.data;
                    this.total = res.data.dataCount;
                }
                this.listLoading = false;
            });
        },
        dataFormat(row, column) {
            let data = row[column.property];
            if (data === null) {
                return "";
            }
            return util.formatDate.format(
                new Date(data.replace(/-/g, "/")),
                "yyyy-MM-dd"
            );
        },
        getStatus(start, end) {
            let result = false;
            let today = util.formatDate.format(new Date(), "yyyy-MM-dd");
            if (
                start &&
                end &&
                util.formatDate.format(
                    new Date(start.replace(/-/g, "/")),
                    "yyyy-MM-dd"
                ) <= today &&
                util.formatDate.format(
                    new Date(end.replace(/-/g, "/")),
                    "yyyy-MM-dd"
                ) >= today
            ) {
                result = true;
            }
            return result;
        },
        handleCurrentChange(val) {
            this.page = val;
            this.getTrucks();
        },
        // getGroupCounts() {
        //     getGroupTruckCount().then((res) => {
        //         if (res.success) {
        //             this.groupCounts = res.data;
        //         }
        //     });
        // },
        groupChange(val) {
            this.getGroupSites(val);
            this.$set(this.editForm, "SITEID", "");
            this.selectSites = this.groupSites;
        },
        //删除
        handleDel(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要删除的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = { STID: row.STID };
                    deleteTruck(para).then((res) => {
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.getTrucks();
                        this.getGroupCounts();
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        async handleEdit(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });

                return;
            }
            this.isEdit = true;
            this.groupChange(row.GROUPID);
            this.editFormVisible = true;
            this.editForm = Object.assign({}, row);
        },
        //显示新增界面
        handleAdd() {
            this.isEdit = false;
            this.editFormVisible = true;
            if (this.groups.length == 1) {
                this.editForm = { GROUPID: this.groups[0]["GROUPID"] };
                this.groupChange(this.groups[0]["GROUPID"]);
            }
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    this.editLoading = true;
                    let para = Object.assign({}, this.editForm);
                    if (this.isEdit) {
                        editTruck(para).then((res) => {
                            if (res.success) {
                                this.$message({
                                    message: "修改成功",
                                    type: "success",
                                });
                                this.$refs["editForm"].resetFields();
                                this.editFormVisible = false;
                                this.editLoading = false;
                                this.getTrucks();
                                this.getGroupCounts();
                            } else {
                                this.editLoading = false;
                                this.$message({
                                    message: res.msg,
                                    type: "error",
                                });
                            }
                        });
                    } else {
                        addTruck(para).then((res) => {
                            if (res.success) {
                                this.$message({
                                    message: "新增成功",
                                    type: "success",
                                });
                                this.$refs["editForm"].resetFields();
                                this.editFormVisible = false;
                                this.editLoading = false;
                                this.getTrucks();
                                this.getGroupCounts();
                            } else {
                                this.editLoading = false;
                                this.$message({
                                    message: res.msg,
                                    type: "error",
                                });
                            }
                        });
                    }
                }
            });
        },
    },
    mounted() {
        this.getTrucks();
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>
<style scoped>
.left-group {
    padding: 0px 10px;
}
.el-select {
    width: 100%;
}
</style>


